table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 18px;
  text-align: left;
}

th, td {
  padding: 12px;
  border-bottom: 1px solid #ddd;
  font-size: 0.8em; /* Adjust this value to your desired smaller size */
}

th {
  background-color: #f2f2f2;
  color: #333;
}

tr:hover {
  background-color: #f5f5f5;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

h3 {
  margin-top: 20px;
}




#propertySelect {
  -webkit-appearance: none; /* Remove default browser styling */
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: none;
  padding: 0;
  margin: 0;
  font-size: 1.5em; /* Adjust to match your title size */
  font-weight: bold;
  color: inherit; /* Inherit text color from parent */
  cursor: pointer; /* Indicate it's interactive */
  outline: none; /* Remove default focus outline */
  /* Add a subtle underline or border on hover/focus */
}

#propertySelect:hover,
#propertySelect:focus {
  /* You can choose one of these or a combination */
  /* border-bottom: 2px solid currentColor; */
  text-decoration: underline;
}

/* Style the arrow/icon of the select box (for better visual) */
.select-wrapper {
  position: relative;
  display: inline-block; /* Allows it to sit next to label if needed */
}

.select-wrapper::after {
  content: '\25BE'; /* Down arrow character */
  position: absolute;
  top: 50%;
  right: -15px;
  transform: translateY(-50%);
  pointer-events: none; /* Prevent the arrow from interfering with clicks */
  color: #444; /* Adjust arrow color */
  font-size: 0.8em;
}

/* Optional: Style the options in the dropdown if needed */
#propertySelect option {
  font-size: 1em;
  font-weight: normal;
  color: #333;
}









